@import "../../global.scss";

.portfolio{
    @media only screen and (max-width: 768px) {
            ul{
                flex-wrap: wrap;
                
                justify-content: center;
            }
          }
    user-select: none;
    //background-color: #fd91a1;
    display: flex;
    flex-direction: column;
    align-items: center;
    ul{
        margin: 10px;
        padding: 0;
        list-style: none;
        display: flex;
       
    }
    .container{
        @media only screen and (max-width: 768px) {
                
            width: 95%;
            
      }
        max-height: 65vh;
        width: 70%;
        display: flex;
        
        justify-content:center;
        flex-wrap:wrap;
        overflow: hidden;
        
        
        .item{
            @media only screen and (max-width: 768px) {
                
                flex-wrap: wrap;
                    margin: 10px 8px 10px 8px;
                    border-radius: 10px;
                    width: 41% ;
                    height: calc(120px + 2vw);
                    
              }
             
            width: calc(150px + 8vw);
            height: calc(120px + 3vw);
            margin: 1vw 1vw;
            display: flex;
            align-items: center;
            justify-content: center;
            position:relative;
            cursor:pointer;
            box-shadow: 0 0 11px rgb(33, 33, 33);
            border-radius: 20px;
            border: 2px solid white;
            transition: all 0.5s ease;
            overflow-y: auto;
            
            h3{
                position :absolute;
                top:20%;
                font-size: calc(13px + 1vw);
                font-style: italic;
                font-weight: 500;
                color:  rgba(255, 255, 255, 0);
                transition: all 0.6s ease;
            }
            .b1{
                position :absolute;
                top:60%;
                left:50%; 
                margin-left:1vw;
            }
            .b2{
                position :absolute;
                top:60%;
                right:50%; 
                margin-right:1vw;
            }

                .icon{
                    display: none;
                    color:rgba(255, 255, 255, 0.747);
                    border: solid 1px white;
                    padding: 5px 10px 5px 10px;
                    align-items: center;
                    justify-content: center;
                    font-size: 30px;
                    border-radius: 8px;
                    background-color: rgba(255, 255, 255, 0.171);
                    transition: all 0.6s ease;
                    @media only screen and (max-width: 768px) {
                        display: block;
                        color:rgba(255, 255, 255, 0.856);   
                        border: solid 1px rgba(255, 255, 255, 0.295);
                        background-color: rgba(0, 0, 0, 0.336);
                    }
                }
                
            
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
                
                               
            }
    
            &:hover{
                transition: all 0.6s ease;
                .icon{
                    display: block;
                    transition: all 0.5s;
                    &:hover{
                        color:rgb(0, 0, 0);
                        background-color: rgba(255, 255, 255, 0.685);
                    }
                }
                transform: scale(1.05, 1.05);
                img{
                    filter: brightness(50%);
                    transition: all 1s ease;
                }
                
                h3{     
                    color:  rgb(255, 255, 255);
                    
                }
            }
            
        }
        
    }
    .txt{
        overflow: hidden;
        user-select: none;
        font-size: calc(40px + 5vw);
        display: flex;
        align-items: center;
        justify-content: center;
        font-style: italic;
        font-weight: 500;
        -webkit-text-stroke: 1.2px $white;
        -webkit-text-fill-color: $black ;
        transition: all 1.5s ease;
        }
        
       
}
