@import "../../global.scss";

.works{
    // background-color: #f08080;

    div{
        overflow: hidden;
        user-select: none;
        height: 50vh;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 10vw;
        font-style: italic;
        font-weight: 500;
        -webkit-text-stroke: 1.2px $white ;
        -webkit-text-fill-color: rgba(0, 0, 0, 0);
        transition: all 1.5s ease;
        }
            align-items: center;
            justify-content: center;
        @keyframes scroll {
            0%{
                transform: translateX(0);
            }
            100%{
                transform: translateX(calc(-200px * 7));
            }
        }
        .slider{
            height: 100px;
            margin:auto;
            overflow: hidden;
            position:relative;
            width: auto;     
        }
        .slider .slider-track{
            animation: scroll 40s linear infinite;
            display: flex;
            flex-direction:row;
            width: calc(250px * 14);
        }
        .slider .slide{
            height: 100px;
            width: 250px;
        }
        
}
